.blazen {
  text-align:center;
  height: 80%;
  width: 15%;
  left: 5%;
  background-color: #333;
  position: fixed; // was sticky. Change to be permanent fixture, rest scrolls.

  background: repeating-linear-gradient(
  90deg,
  #450003,
  #450003 11px,
  #771100,
  #771100 33px,
  #450003,
  #450003 66px,
  #771100,
  #771100 110px,
  #450003,
  #450003 165px,
  #771100,
  #771100 220px);


  > ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    padding-top: 20px;
    > li {
      margin-top: 10px;
      margin-bottom: 10px;
      font-size: 1.5em;
    }
  }
}
.parallax {
  background-image: url("/assets/images/house/SOAL3.jpg");

  height: 100vh;
  // Change to greater than 100vh to enable the same static picture to host more
  // scrolling content
  width: 100vw;

  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  position: relative;
  opacity: 0.65;
}

#slideshow {
  // margin: 30px auto;
  left: 20%;
  position: relative;
  width: 80%;
  height: 240px;
  // padding: 10px;
  box-shadow: 0 0 15px rgba(0,0,0,0.4);
}

#slideshow > div {
  position: absolute;
  top: 10px;
  bottom: 10px;
}

.event {
  width: 100%;
  border: 1px;
  // padding-top: 30px;

  &-image {
    margin: 0 auto;
    width: 220px;
    height: 220px;
    float: left;

    > img {
      max-width: 220px;
      max-height: 220px;
      margin: 0 auto;
    }
  }
  &-date {
    text-align: right;
    margin-right: 30px;
  }
}

// Glass Effect

.glass::before {
  width: 100%;
  height: 100%;
  background-image: url('assets/images/house/SOAL3.jpg');
  background-size: cover;
  content: ' ';
  opacity: 0.4;
  filter: blur(5px);
}
.glass {
  background-color: rgba(255,255,255,0.4);;
}
